* {
	margin: 0 auto;
	padding: 0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-family: "Lato", sans-serif;
	color: #262222;
}
header {
	background-color: #fef8f4;
}
main {
	overflow: hidden;
}
main::-webkit-scrollbar {
	display: none;
}
a {
	color: #262222;
	text-decoration: none;
}

@media only screen and (max-width: 1080px) {
	main {
		padding-top: 100px;
	}
}

/* main navigation */
.mainNavigation {
	position: relative;
	z-index: 2;
}
.mainNavigation_inner {
	display: -ms-grid;
	display: grid;
	width: 90%;
	max-width: 1330px;
	-ms-grid-columns: 1fr 50px 120px 50px 1fr;
	grid-template-columns: 1fr 120px 1fr;
	grid-gap: 50px;
	-ms-flex-line-pack: center;
	align-content: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 30px 0;
}
.mainNavigation_left {
	text-align: right;
	width: 100%;
}
.mainNavigation_right {
	width: 100%;
	text-align: left;
}
.navigationItem {
	display: inline-block;
	vertical-align: middle;
}
.navigationItem {
	margin: 0 35px;
}
.mainNavigation_left .navigationItem:nth-child(1) {
	margin-left: 0px;
}
.mainNavigation_right .navigationItem:nth-last-child(1) {
	margin-right: 0px;
}
.navigationItem.navigationButton {
	border-radius: 30px;
	overflow: hidden;
	font-size: 0;
}
.navigationText a,
.navigationButton a {
	font-size: 20px;
	line-height: 24px;
	padding: 10px 0;
	display: block;
}
.navigationButton a {
	text-transform: uppercase;
	display: inline-block;
	font-size: 18px;
	line-height: 22px;
	padding: 10px 30px;
	font-weight: 700;
	-webkit-transition: all 200ms ease-in-out;
	-o-transition: all 200ms ease-in-out;
	transition: all 200ms ease-in-out;
}
.navigationItem.navigationButton a:nth-child(1) {
	padding-right: 15px;
	background-color: #fbeae1;
}
.navigationItem.navigationButton a:nth-last-child(1) {
	padding-left: 15px;
	background-color: #7ddab8;
}
.navigationButton a:hover {
	color: #fff;
	background-color: #262222 !important;
}
.mainNavigation_center {
	width: 100%;
}
.mainNavigation_center a {
	display: block;
}
.mainNavigationMobile {
	display: none;
}
.mainNavigationMobile_logo a img {
	width: 100%;
	height: auto;
}

@media only screen and (max-width: 1330px) {
	.navigationItem {
		margin: 0 5%;
	}
}
@media only screen and (max-width: 1180px) {
	.navigationItem {
		margin: 0 2.5%;
	}
}
@media only screen and (max-width: 1080px) {
	.mainNavigation {
		display: none;
	}
	.mainNavigationMobile {
		position: fixed;
		display: block;
		z-index: 99;
		background-color: #fef8f4;
		width: 100%;
		top: 0;
		left: 0;
		-webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
		box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
	}
	.mainNavigationMobile_inner {
		position: relative;
		padding: 30px 5%;
	}
	.mainNavigationMobile_top {
		display: -ms-grid;
		display: grid;
		-ms-grid-columns: 100px auto;
		grid-template-columns: 100px auto;
		-ms-flex-line-pack: center;
		align-content: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
	}
	.mainNavigationMobile_logo a {
		display: block;
	}
	.mainNavigationMobile_menuButton {
		font-size: 0;
		width: 100%;
		text-align: right;
	}
	.mainNavigationMobile_menuButton > div {
		display: inline-block;
		vertical-align: middle;
	}
	.navigationItem {
		margin: 0;
		display: block;
	}

	.mainNavigationMobile_menu {
		position: absolute;
		height: calc(100vh - 114px);
		top: 114px;
		left: 0;
		width: 100%;
		background-color: #fef8f4;
		padding: 0 5%;
		text-align: center;
		overflow: hidden;
		-webkit-transform: translateX(100%);
		-ms-transform: translateX(100%);
		transform: translateX(100%);
		-webkit-transition: all 200ms ease-in-out;
		-o-transition: all 200ms ease-in-out;
		transition: all 200ms ease-in-out;
	}
	.active .mainNavigationMobile_menu {
		-webkit-transform: translateX(0%);
		-ms-transform: translateX(0%);
		transform: translateX(0%);
	}
	.active .mainNavigationMobile_menu.navigationItem {
		opacity: 0;
		-webkit-transition: opacity 200ms ease-in-out;
		-o-transition: opacity 200ms ease-in-out;
		transition: opacity 200ms ease-in-out;
	}
	.active .mainNavigationMobile_menu.navigationItem {
		opacity: 1;
	}
	.navigationItem.navigationText {
		text-align: right;
		margin-bottom: 15px;
	}
	.navigationItem.navigationButton {
		display: inline-block;
	}
	.mobileNavigationMenuButton {
		padding-left: 20px;
	}
	.menuButton {
		width: 50px;
		height: 50px;
		background-image: url(../pigeonImages/mobileMenu.png);
		background-position: center center;
		background-size: contain;
		background-repeat: no-repeat;
	}
	.mainNavigationMobile .ctaButton {
		display: inline-block;
		width: 100%;
		max-width: 240px;
	}
	.mainNavigationMobile .ctaButton_inner a {
		padding: 10px 0px;
		text-align: center;
	}
}

@media only screen and (max-width: 880px) {
	.mainNavigationMobile .ctaButton {
		width: auto;
	}
	.mainNavigationMobile .ctaButton_inner a {
		padding: 10px 20px;
		font-size: 14px;
	}
}

@media only screen and (max-width: 400px) {
	.mainNavigationMobile_top {
		grid-template-columns: 70px auto;
	}
	.mainNavigationMobile .ctaButton_inner a {
		line-height: 20px;
	}
	.menuButton {
		height: 30px;
		width: 30px;
	}
}
@media only screen and (max-width: 300px) {
	.mainNavigationMobile .ctaButton {
		display: none;
	}
}

/* floating cta */
.headerScrollCta {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 60px;
	opacity: 0;
	z-index: 1;
	-webkit-transition: opacity 200ms ease-in-out;
	-o-transition: opacity 200ms ease-in-out;
	transition: opacity 200ms ease-in-out;
}
.headerScrollCta.active {
	opacity: 1;
	z-index: 99;
}
.headerScrollCta_inner {
	width: 90%;
	max-width: 1330px;
	text-align: right;
}

@media only screen and (max-width: 1080px) {
	.headerScrollCta {
		display: none;
	}
}

